





















































































































































































































































































































































    declare global {
        interface Window{ PrismicJS: any, PrismicDOM: any, tns: any }
    }	

    import { Component, Vue, Watch } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';

	Component.registerHooks(['beforeRouteEnter']);

	@Component
	export default class About extends Vue {
		prismicDOM = window.PrismicDOM;
		about: any = {};
		slider:any = null;
		sermon: any = {};
		devotional: any = {};
		player: HTMLMediaElement;
		progressBar: HTMLElement;
		isMuted = false;
		isPlaying = false;
		currentTime = '';
		duration = '';
		showTime = false;
		remainingTime = '';

        //overlay = 'linear-gradient(rgba(7, 61, 95, 0.6), rgba(7, 61, 95, 0.6)),';
		//overlay = 'linear-gradient(rgba(7,61,95, 0.4), rgba(7,61,95, 0.4)),';
		overlay = 'linear-gradient(rgba(0,0,0, 0.4), rgba(0,0,0, 0.4)),';
		overlay_orange = 'linear-gradient(rgba(219, 123, 29, 0.8), rgba(219, 123, 29, 0.8)),';

        private services: any[] = [
            { title: 'BIBLE STUDY', url: '/events'},
            { title: 'PRAYER MEETING', url: '/events'},
            { title: 'CONNECT GROUP', url: '/about-us/ministry' },
		];
		
		@Watch('about', {deep: true}) _iniSlides(){
            setTimeout(()=>
            this.slider = window.tns({
                container: '.home-slider',
                slideBy: 'page',
                autoplay: true,
                controls: false,
                nav: false,
                arrowKeys: true,
                autoPlayHoverPause: true,
                autoplayButtonOutput: false,
                speed: 950,
			    mode: 'gallery',
                autoplayTimeout: 9000
            }),5);
		}
		
		@Watch('sermon', {deep: true}) _sermons() {
			setTimeout(() =>{
				//this.progressBar = document.getElementById('progress-bar');
				/*this.progressBar = document.getElementById('slide__progress-span');
				this.progressBar.style.width = 100 + '%';*/
				this.player = document.getElementById('lastest-sermon') as HTMLMediaElement;
				this.player.onloadedmetadata = () => this.remainingTime = this.formatTime(this.player.duration);
				this.player.onplay  = (e) => { this.isPlaying = true; this.showTime = true; };
				this.player.onpause = (e) => this.isPlaying = false;
				this.player.onended = (e) => this.isPlaying = false;
				
				this.player.ontimeupdate = (e) => {
					//this.currentTime = this.formatTime(this.player.currentTime);
					//this.duration = this.formatTime(this.player.duration);
					this.remainingTime = this.formatTime(this.player.duration - this.player.currentTime);
					/*var percentage = Math.floor((100 / this.player.duration) * this.player.currentTime);
					this.progressBar.value = percentage;
					this.progressBar.innerHTML = percentage + '% played';*/
				}
			},2);
		}

        beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => { vm.getPage(); vm.getSermons(); });
		}

		/*mounted(){
			this.player = document.getElementById('lastest-sermon') as HTMLMediaElement;
			this.player.onplay  = (e) => this.isPlaying = true;
			this.player.onpause = (e) => this.isPlaying = false;
			this.player.onended = (e) => this.isPlaying = false;
		}*/
		
		async getSermons() {
			var api = await _Prismic();;
			this.sermon = (await api.query(
                window.PrismicJS.Predicates.at('document.type', 'sermon'), 
                { pageSize : 1, page : 1, fetch:['sermon.video', 'sermon.title', 'sermon.date', 'sermon.minister'], orderings : '[document.first_publication_date desc]'}
			)).results[0];

			this.devotional = (await api.query(
                window.PrismicJS.Predicates.at('document.type', 'devotional'), 
                { pageSize : 1, page : 1, fetch:['devotional.title', 'devotional.verses'] }
            )).results[0];
		}

        async getPage() {
			let api = await _Prismic();
			let response = await api.query(window.PrismicJS.Predicates.at('document.type', 'about'),
				{ fetch:[ 'about.leadership', 'about.top_image', 'about.middle_image', 'about.bottom_image' ]}
            );
		    this.about = response.results[0].data;
        }

        backgroundImage(url, color) {
			if(color && color == 'orange')
				return this.overlay_orange + 'url("'+ url +'")';	
            return this.overlay + 'url("'+ url +'")';
        }

		scrollTo(id: string) {
			var el = document.getElementById(id);
			el.scrollIntoView({behavior: 'smooth'});
		}

		play() {
			this.player.play();
			this.isPlaying = true;
		}

		pause() {
			this.player.pause(); 
            this.isPlaying = false;
		}

		mute() {
			this.player.muted = true;
			this.isMuted = true;
		}

		unmute() {
			this.player.muted = false; 
			this.isMuted = false;
		}

		formatTime(seconds, guide = seconds) {
			 seconds = seconds < 0 ? 0 : seconds;
			let s = Math.floor(seconds % 60);
			let m = Math.floor(seconds / 60 % 60);
			let h = Math.floor(seconds / 3600);
			const gm = Math.floor(guide / 60 % 60);
			const gh = Math.floor(guide / 3600);
			
			// handle invalid times
			if (isNaN(seconds) || seconds === Infinity) {
				// '-' is false for all relational operators (e.g. <, >=) so this setting
				// will add the minimum number of fields specified by the guide
				h = m = s = 0 ; //'-';
			}

			// Check if we need to show hours
			//h = (h > 0 || gh > 0) ? h + ':' : '';
			var hours = (h > 0 || gh > 0) ? h + ':' : '';

			// If hours are showing, we may need to add a leading zero.
			// Always show at least one digit of minutes.
			//m = (((h || gm >= 10) && m < 10) ? '0' + m : m) + ':';
			var minute  = (((h || gm >= 10) && m < 10) ? '0' + m : m) + ':';


			// Check if leading zero is need for seconds
			//s = (s < 10) ? '0' + s : s;
			var second = (s < 10) ? '0' + s : s;

			return hours + minute + second;
		}

		/*updateProgressBar() {
			var progressBar = document.getElementById('progress-bar');
			var percentage = Math.floor((100 / this.player.duration) *
			this.player.currentTime);
			progressBar.value = percentage;
			progressBar.innerHTML = percentage + '% played';
		}*/


}
